import React from 'react'
import { graphql, Link } from 'gatsby'
import { kebabCase } from 'lodash'
import Layout from '../components/Layout'
import Tag from '../components/Tag'

import { useSpring, animated } from 'react-spring'

const AnimFeTurbulence = animated('feTurbulence')
const AnimFeDisplacementMap = animated('feDisplacementMap')

const PostLink = ({ post }) => (
	<div className='flex justify-between my-8'>
		<Link to={post.frontmatter.slug}>
			<h2 className='text-lg font-semibold'>{post.frontmatter.title}</h2>
			{post.frontmatter.location} – {post.frontmatter.commitment}
		</Link>

		{/* tags */}
		<div>
			<ul className='taglist'>
				{(post.frontmatter.tags && post.frontmatter.tags.length)
					? post.frontmatter.tags.map(tag => (
						<li key={tag + `tag`} className='inline'>
							<Link to={`/tags/${kebabCase(tag)}/`}>
								<Tag key={tag} tag={tag} />
							</Link>
						</li>
					))
					: null}
			</ul>
		</div>
	</div>
)

export default ({
	data: {
		allMarkdownRemark: { edges },
	},
}) => {
	const Posts = edges
		// .filter(edge => !!edge.node.frontmatter.date) // You can filter your posts based on some criteria
		.map(edge => <PostLink key={edge.node.id} post={edge.node} />)

	console.log(edges)

	const { freq, scale, transform, opacity } = useSpring({
		from: { scale: 10, opacity: 0.3, transform: 'scale(0.9)', freq: '0.0175, 0.0' },
		to: { scale: 15, opacity: 1, transform: 'scale(1)', freq: '0.0, 0.0' },
		config: { duration: 3000 },
	})

	return (
		<Layout includePreFooter>
			<div className='py-48 text-6xl font-hairline text-center text-purple-100 bg-gradient-to-r from-orange-400 via-red-500 to-indigo-500'>
				<animated.svg height={0}>
					<defs>
						<filter id='water'>
							<AnimFeTurbulence type='fractalNoise' baseFrequency={freq} numOctaves='1.5' result='TURB' seed='8' />
							<AnimFeDisplacementMap xChannelSelector='R' yChannelSelector='G' in='SourceGraphic' in2='TURB' result='DISP' scale={scale} />
						</filter>
					</defs>
				</animated.svg>
				<animated.p style={{ filter: 'url(#water)', transform: transform, opacity: opacity }}>
					We are hiring!
				</animated.p>
			</div>
			<div className='container flex-grow mx-auto mt-8'>
				<div className='mx-4'>
					{Posts}
				</div>
			</div>
		</Layout>
	)
}

export const pageQuery = graphql`
  query {
    allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/.*careers.*/"}}, sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          frontmatter {
            slug
            title
            location
            commitment
            tags
          }
        }
      }
    }
  }
`
